<template>
    <div id="app">
        <Header></Header>
        <Breadcrumb name="Á∞°ÁπÅËΩâÊèõ" slug="translator" root="/app/translator" :feedbackEnable="true" :crumbEnable="true">
            <img slot="logo" svg-inline :src="getIcon('translator')" />
        </Breadcrumb>
        <LeftSidebar :open="false">
            <Nav />
        </LeftSidebar>
        <Main :withoutRight="true" :withoutLeft="true">
            <div class="m-translator">
                <h1 class="title">Á∞°ÁπÅËΩâÊèõÂ∑•ÂÖ∑</h1>
                <h3 class="title">Â∞áÂÆèÊàñÊèí‰ª∂Êï∏ÊìöÁ≠âËΩâÁÇ∫ÂäçÁ∂≤‰∏âÂúãÈöõÊúçÂ∞àÁî®ÁπÅÈ´î</h3>
                <el-tabs type="card" class="translate-wrapper" :before-leave="tabClick" v-model="activeTabName">
                    <el-tab-pane label="ÊñáÂ≠óËΩâÊèõ" :disabled="isLoading" name="translate-str">
                        <div class="translate-content">
                            <el-input
                                type="textarea"
                                placeholder="ËØ∑ËæìÂÖ•ÂéüÊñáÔºàÁÆÄ‰ΩìÔºâÂÜÖÂÆπ„ÄÇÂª∫ËÆÆ‰∏çË¶ÅË∂ÖËøá1000‰∏™Ê±âÂ≠ó„ÄÇÂ§ßÊÆµÊñáÂ≠óÂÆπÊòìÂØºËá¥ÊµèËßàÂô®Âç°Ê≠ªÔºåÂª∫ËÆÆ‰ΩøÁî®Êñá‰ª∂ËΩ¨Êç¢ÂäüËÉΩ„ÄÇ"
                                v-model="preTranslateText"
                                :validate-event="false"
                                resize="none"
                                maxlength="10000"
                                show-word-limit
                                class="textarea-translate textarea-translate-pre"
                            ></el-input>
                            <el-input
                                type="textarea"
                                placeholder="ËΩâÊèõÔºàÁπÅÈ´îÔºâ‰πãÂæåÁöÑÂÖßÂÆπ"
                                v-model="postTranslateText"
                                :validate-event="false"
                                resize="none"
                                :readonly="false"
                                class="textarea-translate textarea-translate-post"
                            ></el-input>
                        </div>
                        <el-button type="primary" :loading="isLoading" class="btn-convert" size="medium" @click="convertText" v-if="percentage < 0">{{ isLoading ? "Ê≠£Âú®Âä†Ëºâ" : "ËΩâÊèõ" }}</el-button>
                        <el-progress :text-inside="true" :stroke-width="20" :percentage="percentage" v-if="isLoading && percentage >= 0"></el-progress>
                    </el-tab-pane>
                    <el-tab-pane label="Êñá‰ª∂ËΩâÊèõ" :disabled="isLoading" name="translate-file">
                        <transition name="el-zoom-in-top">
                            <el-alert title="ËΩ¨Êç¢ÊàêÂäü!" type="success" show-icon v-if="downloadFileUrl !== ''">
                                Â¶ÇÊûúÊ≤íÊúâËá™Âãï‰∏ãËºâÁöÑË©±,
                                <el-button type="text" class="btn-download" @click="downloadByUrl">ÈªûÊ≠§‰∏ãËºâ</el-button>
                            </el-alert>
                        </transition>
                        <h4>‰∏ä‰º†ÁöÑÊñá‰ª∂ÁºñÁ†Å</h4>
                        <el-radio-group v-model="fileEncoding">
                            <el-radio label="gbk" border>GBK</el-radio>
                            <el-radio label="utf-8" border>UTF-8</el-radio>
                        </el-radio-group>
                        <h5 class="hint-inline">Â¶ÇÊûú‰Ω†‰∏çÁü•ÈÅìËøôÊòØ‰ªÄ‰πàÔºåËØ∑‰∏çË¶Å‰øÆÊîπÊ≠§ÈÄâÈ°π</h5>
                        <div class="translate-content">
                            <el-upload
                                class="upload-file"
                                drag
                                action="javascript:void(0)"
                                :auto-upload="true"
                                :show-file-list="false"
                                v-loading="isLoading"
                                element-loading-text="Ê≠£Âú®ËΩâÊèõ,Ë´ãÁ®çÂæå"
                                :http-request="uploadFile"
                                v-if="percentage < 0"
                            >
                                <i class="el-icon-upload"></i>
                                <div class="el-upload__text">
                                    Â∞áÊñá‰ª∂ÊãñÂà∞Ê≠§Ëôï,Êàñ
                                    <em>ÈªûÊìä‰∏äÂÇ≥</em>
                                </div>
                                <div class="el-upload__tip" slot="tip">Êö´‰∏çÊîØÊåÅwordÁ≠âÂ∏∂Ê®£ÂºèÊñáÊ™î</div>
                            </el-upload>
                            <el-progress type="circle" :percentage="percentage" v-if="isLoading && percentage >= 0" :format="progressFormat"></el-progress>
                        </div>
                    </el-tab-pane>
                    <el-tab-pane label="üíñË≤¢ÁçªË©ûÂ∫´" :disabled="isLoading" name="add-dict"></el-tab-pane>
                </el-tabs>
            </div>
            <Footer></Footer>
        </Main>
    </div>
</template>

<script>
import Nav from "@/components/Nav.vue";
import { axios } from "@/service/api.js";
import User from "@jx3box/jx3box-common/js/user";
var blob = new Blob([document.querySelector("#worker").textContent]);
var url = window.URL.createObjectURL(blob);
var worker = new Worker(url);
import dict from "@jx3box/jx3box-dict/dict.json";
import { __imgPath } from "@jx3box/jx3box-common/data/jx3box.json";
export default {
    name: "Translator",
    data: function() {
        return {
            worker: null,
            activeTabName: "translate-str",
            preTranslateText: "",
            postTranslateText: "",
            isLoading: false,
            percentage: -1,
            downloadFileUrl: "",
            dict,
            fileEncoding: "gbk",
        };
    },
    computed: {},
    methods: {
        getIcon(key) {
            return __imgPath + "image/box/" + key + ".svg";
        },
        tabClick(activeName, oldName) {
            if (activeName === "add-dict") {
                var aTag = document.createElement("a");
                aTag.href = "https://github.com/JX3BOX/jx3box-dict";
                aTag.target = "_blank";
                aTag.click();
                return false;
            } else {
                return true;
            }
        },
        progressFormat(percentage) {
            return percentage === 100 ? "ÂÆåÊàê" : `${percentage}% \nÊ≠£Âú®ËΩâÊèõ...`;
        },
        initWorker() {
            worker.addEventListener("message", (event) => {
                let data = event.data;
                let progress = 0;
                if (data.success) {
                    switch (data.type) {
                        case "str":
                            this.postTranslateText = data.result;
                            this.isLoading = false;
                            this.percentage = -1;
                            break;
                        case "file":
                            this.downloadTranslatedFile(data.result);
                            this.isLoading = false;
                            this.percentage = -1;
                            break;
                        case "progress":
                            this.percentage = data.result;
                            break;
                        default:
                            break;
                    }
                }
            });
            worker.addEventListener("error", (event) => {
                this.$message.error("ËΩâÊèõÂá∫ÈåØ");
                console.log(["ERROR: Line ", e.lineno, " in ", e.filename, ": ", e.message].join(""));
            });
        },
        getDict() {
            let url = `https://cdn.jsdelivr.net/gh/JX3BOX/jx3box-dict@master/dict.json?v=${Date.now()}`;
            axios(url)
                .then((response) => {
                    this.dict = response;
                    this.isLoading = false;
                })
                .catch((e) => {
                    switch (e.code) {
                        case -1:
                            // ÁΩëÁªúÂºÇÂ∏∏
                            this.$message.error(e.msg);
                            break;
                        default:
                            // ÊúçÂä°Âô®ÈîôËØØ
                            console.log(e);
                            this.$message.error(`[${e.code}]${e.msg}`);
                    }
                });
        },
        async convertText() {
            let preText = this.preTranslateText;
            if (preText.replace(/\ /g, "") === "") {
                return;
            }
            // let isLogin = true;
            this.isLoading = true;
            // if (preText.length > 1000) {
            //     // isLogin = await this.checkLogin();
            //     isLogin = User.isLogin()
            //     if (!isLogin) {
            //         this.$message.warning(
            //             "ËΩ¨Êç¢ÁöÑÂ≠óÁ¨¶Ë∂ÖËøá1000‰∏™ÈúÄË¶ÅÂÖàÁôªÂΩïÂÜçÂ∞ùËØïËΩ¨Êç¢~"
            //         );
            //         this.isLoading = false;
            //         return;
            //     }
            // }
            this.percentage = 0;
            worker.postMessage({
                cmd: "translate-str",
                str: preText,
                dict: this.dict,
            });
        },
        async uploadFile(file) {
            if (file.file) {
                let isLogin = true;
                this.isLoading = true;
                if (file.file.size > 1024 * 1024) {
                    isLogin = await this.checkLogin();
                    if (!isLogin) {
                        this.$message.warning("ËΩ¨Êç¢ÁöÑÊñá‰ª∂Â§ßÂ∞èË∂ÖËøá1MBÈúÄË¶ÅÂÖàÁôªÂΩïÂÜçÂ∞ùËØïËΩ¨Êç¢~");
                        this.isLoading = false;
                        return;
                    }
                }
                this.downloadFileUrl = "";
                this.filename = file.file.name;
                this.percentage = 0;
                worker.postMessage({
                    cmd: "translate-file",
                    file: file.file,
                    dict: this.dict,
                    encoding: this.fileEncoding,
                });
            }
        },
        downloadTranslatedFile(blob) {
            var aTag = document.createElement("a");
            aTag.download = `JX3BOXËΩâÊèõ-${this.filename}`;
            let url = URL.createObjectURL(blob);
            aTag.href = url;
            this.downloadFileUrl = url;
            aTag.click();
            this.isLoading = false;
        },
        downloadByUrl() {
            if (this.downloadFileUrl === "") {
                return;
            }
            var aTag = document.createElement("a");
            aTag.download = `JX3BOXËΩâÊèõ-${this.filename}`;
            let url = this.downloadFileUrl;
            aTag.href = url;
            aTag.click();
        },
        async checkLogin() {
            let isLogin = true;
            if (!this.uid) {
                isLogin = false;
            } else {
                isLogin = await this.testCheckIsLogin();
            }
            return new Promise((resolve, reject) => {
                resolve(isLogin);
            });
        },
        // Áî®‰∫éÈ™åËØÅÊòØÂê¶ÁúüÁöÑÁôªÂΩï‰∫Ü
        testCheckIsLogin() {
            return User.isLogin();
        },

        // ÂàÜÂâ≤Á∫ø
        getUserId() {
            if (User.isLogin()) {
                this.uid = User.getInfo().uid;
            }
        },
    },
    filters: {},
    created() {
        this.initWorker();
        this.getDict();
    },
    mounted: function() {
        this.getUserId();
    },
    beforeDestroy() {
        worker.terminate();
    },
    components: {
        Nav,
    },
};
</script>

